const Post = (props) => {
    return ( 
        <React.Fragment>
            <div class="card text-white bg-info mb-3" >
                <div class="card-header">{props.post.titulo}</div>
                <div class="card-body">
                    <h4 class="card-title">Info card title</h4>
                    <p class="card-text">{props.post.cuerpo}</p>
                </div>
            </div>
        </React.Fragment>

     );
}
 
export default Post;